

* {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
}

a {
  text-decoration: none;
}

ul li {
  list-style: none;
  display: inline;
}

button {
  outline: none;
  border: none;
  background: transparent;
  cursor: pointer;
}

/* 
----------------------------------------
Layout & Component Styles
----------------------------------------
*/

.App {
  margin: 0 auto;
  padding: 0 20px;
}

.filter_menu .row span {
  display: none;
}

.login-page {
  padding: 30px 15px;
}

.delete-all {
  margin: 20px 0;
}

.create_product .upload {
  margin: 20px 0;
}

/* 
----------------------------------------
Responsive Styles
----------------------------------------
*/

@media (max-width: 820px) {
  header .logo {
      min-width: 115px;
      flex: none;
  }

  header ul {
      padding: 10px 0;
  }
}

@media (max-width: 720px) {
  header {
      justify-content: space-between;
  }

  header ul {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100vh;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      opacity: 0.9;
      z-index: 99;
      transition: 0.5s ease-in;
  }

  header .menu {
      display: block;
      cursor: pointer;
  }

  header ul li .menu {
      position: absolute;
      top: 20px;
      right: 20px;
  }
}

@media (max-width: 570px) {
  .filter_menu .row span {
      display: none;
  }
}

@media (max-width: 500px) {
  .App {
      padding: 0 10px;
  }

  .detail {
      font-size: 100%;
      padding: 0;
  }

  .filter-menu input {
      margin: 0;
  }

  .filter-menu .sort {
      flex: 1;
  }

  .filter-menu .sort select {
      width: 100%;
      min-width: 290px;
      margin: 5px 0;
  }
}

