.load-page{
    background: #fff;
    overflow: hidden;
}
.loader{
    width: 200px;
    height: 200px;
    margin: auto;
}
.loader div{
    width: calc(100% -15px);
    height: calc(100% - 15px);
    border:2px solid #fff;
    border-top: 2px solid #91C355;
    border-radius: 50%;
    animation: rotate 10s linear infinite alternate-reverse;
}

@keyframes rotate{
    50%{ transform: rotate(80deg);}
    100%{transform: rotate(360deg);}
}